* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-image: url("fondo.6ca645da.png");
  background-repeat: repeat;
  background-size: auto;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  font-family: Arial, sans-serif;
  display: flex;
}

.container {
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  padding: 20px;
  display: flex;
}

.welcome-page {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 40px;
  width: 100%;
  max-width: 500px;
  display: flex;
}

.title-image {
  width: 100%;
  max-width: 350px;
  height: auto;
  animation: 1s ease-in fadeIn;
}

.start-button {
  cursor: pointer;
  background: none;
  border: none;
  padding: 0;
  transition: transform .2s;
}

.start-button:hover {
  transform: scale(1.05);
}

.start-button:active {
  transform: scale(.95);
}

.start-button img {
  width: 200px;
  height: auto;
  display: block;
}

.hands-container {
  justify-content: center;
  align-items: center;
  gap: 30px;
  animation: 1.5s ease-in fadeIn;
  display: flex;
}

.hand-icon {
  width: 80px;
  height: auto;
  transition: transform .3s;
}

.hand-icon:hover {
  transform: translateY(-10px)rotate(5deg);
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (width <= 600px) {
  .title-image {
    max-width: 280px;
  }

  .start-button img {
    width: 160px;
  }

  .hands-container {
    gap: 20px;
  }

  .hand-icon {
    width: 60px;
  }
}
