* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Arial", sans-serif;
  min-height: 100vh;
  background-image: url("./images/fondo.png");
  background-repeat: repeat;
  background-size: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.welcome-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 40px;
  max-width: 500px;
  width: 100%;
}

.title-image {
  width: 100%;
  max-width: 350px;
  height: auto;
  animation: fadeIn 1s ease-in;
}

.start-button {
  background: none;
  border: none;
  cursor: pointer;
  transition: transform 0.2s ease;
  padding: 0;
}

.start-button:hover {
  transform: scale(1.05);
}

.start-button:active {
  transform: scale(0.95);
}

.start-button img {
  width: 200px;
  height: auto;
  display: block;
}

.hands-container {
  display: flex;
  gap: 30px;
  align-items: center;
  justify-content: center;
  animation: fadeIn 1.5s ease-in;
}

.hand-icon {
  width: 80px;
  height: auto;
  transition: transform 0.3s ease;
}

.hand-icon:hover {
  transform: translateY(-10px) rotate(5deg);
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive */
@media (max-width: 600px) {
  .title-image {
    max-width: 280px;
  }

  .start-button img {
    width: 160px;
  }

  .hands-container {
    gap: 20px;
  }

  .hand-icon {
    width: 60px;
  }
}
